<template>
  <div id="main" class="m-padding-tb-max m-margin-top-small">
    <div class="ui container stackable">
      <uv-statistic></uv-statistic>
      <div class="ui segment basic center right aligned">
        <div class="center aligned stackable container ui side-container">
          <div id="flip_left" class="ui basic button teal circular">
            <img src="http://cdn.panzvor.com/double-left.png" alt class="circular-img" />
          </div>
          <div id="flip_right" class="ui teal basic button circular">
            <img src="http://cdn.panzvor.com/double-right.png" alt class="circular-img" />
          </div>
          <div class="segment basic center aligned ui shape cube m-cube">
            <div class="ui sides">
              <div class="active side m-shape-side container stackable ui">
                <img class="ui image" alt src="http://cdn.panzvor.com/kieed-miku.gif" />
              </div>
              <side aboutTitle="" :aboutContent="aboutBlog">
                <template #extra>
                  <appreciate></appreciate>
                </template>
              </side>
              <side aboutTitle="" :aboutContent="aboutAuthor">
                <template #extra>
                  <contact></contact>
                </template>
              </side>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { scrollEventListener } from "common/mixin";
import UvStatistic from "components/uvStatistic/UVStatistic";

export default {
  name: "About",
  components: {
    side: () => import("./unit/Side"),
    appreciate: () => import("./unit/Appreciate"),
    contact: () => import("./unit/Contact"),
    UvStatistic
  },
  data() {
    return {
      aboutAuthor: `<h4 style='margin-bottom: 1.5em;'>2021CodeDrawRun</h4>
      
      
      
      
      `,
      aboutBlog: `
      <strong></strong>Vue.js 2.5<br>
      <strong>UI</strong>Semantic UI 2.4<br>
      <strong></strong>SpringBoot 2.3.0<br>
      <strong></strong>MyBatis 3.5.4<br>
      <strong></strong>MySQL 5.7.3Redis 3.2.1<br>
      VueVueB4000
      SpringCloud
      
      `
    };
  },
  mounted() {
    $("#flip_right").click(function() {
      $(".ui.cube.shape")
        .shape()
        .shape("flip right");
    });
    $("#flip_left").click(function() {
      $(".ui.cube.shape")
        .shape()
        .shape("flip left");
    });
  },
  mixins: [scrollEventListener]
};
</script>

<style scoped>
@media only screen and (max-width: 800px) {
  .ui.basic.teal.button {
    border: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
  .circular-img {
    opacity: 0.4;
  }
  #flip_left:active {
    background-color: rgb(255, 193, 255) !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
  #flip_right:active {
    background-color: rgb(255, 193, 255) !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
  }
}
.side-container {
  position: relative;
}
#flip_left {
  position: absolute;
  left: 0;
  top: 50%;
  z-index: 9;
}
#flip_right {
  position: absolute;
  right: 0;
  top: 50%;
  z-index: 9;
}
#flip_left:active {
  background-color: rgb(255, 255, 255) !important;
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
}
#flip_right:active {
  background-color: rgb(255, 255, 255) !important;
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
}
.circular {
  border-radius: 50% !important;
  width: 4em;
  height: 4em;
  position: relative;
}
.circular-img {
  border-radius: 50% !important;
  width: 2.5em;
  height: 2.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>